import { IchangeValues } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { PsBaseFieldComponent } from 'src/app/commonSRC/psComponents/ps-base/ps-base-field/ps-base-field.component';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsComplexCifAccountExposed } from './ps-complex-cif-account.component.interface';

@Component({
  selector: 'ps-complex-cif-account',
  templateUrl: './ps-complex-cif-account.component.html',
  styleUrls: ['./ps-complex-cif-account.component.scss'],
})
export class PsComplexCifAccountComponent extends PsBaseFieldComponent implements OnInit {
  @Input() options: IOptionsPsComplexCifAccountExposed;
  @Output() cifValidated: EventEmitter<any> = new EventEmitter(); 

  cifNumberOptions: IOptionsPsComplexCifAccountExposed = {
    fcName: 'cifAccount',
    labelKey: 'cif_no_key',
    placeHolder: 'enter_cif_no_key',
  };

  constructor(
    commonService: PsCommonService,
    logger: LoggerService,
    private omniPull?: OmniPullService
  ) {
    super(commonService, logger);
  }

  ngOnInit() {
    this.commonProv.copyObject(this.cifNumberOptions, this.options);
    this.cifNumberOptions.forceTriggerChange = true;
    super.init();
  }

  async onCifChange(value: IchangeValues) {
    const cifNumber = this.options.group.controls[this.cifNumberOptions.fcName].value;

    if (!cifNumber) {
      CommonUtils.presentFailureAlert(
        this.commonProv.translate('enter_value_before_proceed_key'),
        { autoHide: true }
      );
      return;
    }

    const requestData: any = {
      commonParametersList: {
        userCifNo: cifNumber
      }
    };

    try {
      const result = await this.omniPull.returnCIFDetails(requestData);
      const cifDetails = result.data.result?.[0]?.cifDetails;
      if (result?.data?.outputCode === 0 && cifDetails && Object.keys(cifDetails).length > 0) {
        this.cifValidated.emit(result.data);
      } else {
        this.clearCifField('invalid_cif_key');  
      }
    } catch (error) {
      console.error('Error validating CIF:', error);
      this.clearCifField('invalid_cif_key');
    }
  }

  /**
   * Clears the CIF field and displays an error message
   * @param messageKey - Translation key for the error message (optional)
  */
  private clearCifField(messageKey?: string): void {
    this.options.group.controls[this.cifNumberOptions.fcName].setValue(null);
    if (messageKey) {
      CommonUtils.presentFailureAlert(this.commonProv.translate(messageKey));
    }
  }

  /**
   * Resets the CIF account field
  */
  resetCifField(): void {
    this.clearCifField();
  }
}
